import { fireEvent, render, waitFor } from '@testing-library/react-native';

import AsyncStorage from '@react-native-async-storage/async-storage';
import React from 'react';
import { CameraComponent } from '../src/components/';
import HomeScreen from '../src/screen/Home'; // ajuste o path

// Mock do AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () => ({
  getItem: jest.fn(),
  setItem: jest.fn(),
}));

// Mock do CameraComponent
jest.mock('../src/components/', () => ({
  CameraComponent: jest.fn(() => null),
}));

// Mock do Ionicons
jest.mock('react-native-vector-icons/Ionicons', () => 'Icon');

// Mock do useNavigation
const mockNavigate = jest.fn();
jest.mock('@react-navigation/native', () => ({
  ...jest.requireActual('@react-navigation/native'),
  useNavigation: () => ({
    navigate: mockNavigate,
  }),
}));

describe('HomeScreen', () => {
  beforeEach(() => {
    (AsyncStorage.getItem as jest.Mock).mockResolvedValue(null);
    mockNavigate.mockClear();
  });

  it('should render search input and camera button', async () => {
    const { getByPlaceholderText } = render(<HomeScreen />);

    expect(getByPlaceholderText('Buscar por data...')).toBeTruthy();
  });

  it('should open camera when camera button is pressed', async () => {
    const { getByTestId } = render(<HomeScreen />);

    const cameraButton = getByTestId('camera-button');
    fireEvent.press(cameraButton);

    await waitFor(() => {
      expect(CameraComponent).toHaveBeenCalled();
    });
  });

  it('should toggle dark mode', () => {
    const { getByTestId } = render(<HomeScreen />);

    const themeButton = getByTestId('theme-button');
    fireEvent.press(themeButton);

    // Não precisa de assert explícito aqui, mas você poderia testar mudança de estilo se quiser
  });
});
